html {

}
body {
  font-size: $fz-default;
  font-family: $ff-gotham;
  // line-height: 1;
  &.lock {
    overflow-y: hidden;
  }
}
.wrapper {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  & main {
    flex-grow: 1;
  }
}
.container {
  max-width: 1160px;
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin: 0 auto;
  line-height: 36px;
  &__title {
    font-size: $fz-title;
    font-weight: 700;
  }
  &__subtitle {
    font-size: $fz-subtitle;
    font-weight: 500;
  }
}



.ruble {
  &::after {
    content: ' ₽';
    font-weight: 400;
  }
}

.section-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
}

.section-title-small {
  font-size: 18px;
  font-weight: 700;
}


